.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #423F3E;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  width: 100vw;
  height: 88vh;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  
}
.title{
  color: white;
  padding: 10px;
  text-align: center;
}

h2 {
  margin: 0px;
}
.cardMobile {
  display: flex;
  width: 90vw;
  flex-direction: column;
  align-items: center;
}
/* Telefono */

@media (max-width: 768px) {
  .container {
    align-items: center;
    width: 90vw;
    margin: auto;
    justify-content: flex-start;
    flex-direction: column;
  }
  
}

.mobileDisplay{
  width: 100%;
}

.closeButton{
  background-color: #212121 ;
  border: 0px;
  display: flex;
  margin-top: 1em;
  margin-left: 1em;
  align-items: center;
  justify-items: center;
  color: white;
}

.closeButton > span{
  padding-left: 12px;
}

.previewCardContiner{
  width: 40%;
  margin-top: 3em;
  margin-right: 2em;
}