.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 40%;
    height: 90%;
    margin: 3em;
    margin-top: 3em;
    overflow: auto;
    background-color: #424242;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 10px;

}

.commentContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    background-color: #525252;
    
    height: 100%;
}

.comments {
    overflow: auto;
    margin: 1em;
    margin-top: 0em;
    height: 100%;
}

.comments::-webkit-scrollbar {
    width: 10px;
}

.comments::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: rgba(34, 34, 34, 0.39);
}

.comment{
    color: white;
    margin-top: 1em;
    padding-left: 1em;
    padding-bottom: 0.5em;
    padding-top: 0.5em;
    background-color: #313131;
    border-radius: 10px;
}

.user{ 
    font-weight: bold ;
}

.padding{
    padding-top: 0.5em;
    padding-left: 1em;
    margin-bottom: 1em;
}
.commentBoxContainer{
    color: white;
    background-color: #313131;
    padding-bottom: 1.5em;
}
.noComments{
    color: white;
    padding: 1em;

    background-color: #313131;
    
}

.form{
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.form > form {
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.form > form > textarea {
    width: 400px;
    overflow: visible;
    margin-right: 2em;
    background-color: #313131;
    border-radius: 10px;
    padding-left: 1em;
    padding-top: 1em;
    resize: none;
    color: white;
} 

/* Tablet */
@media (max-width: 1024x) {
    .container{
        width: 100%; 
    }
    .form > form > textarea {
        width: 35%;
    }
}


/* Telefono */
@media (max-width: 768px) {


    .container{
        width: 90%; 
        margin: auto;
        margin-top: 1em;
    }
    .form > form > textarea {
        width: 50%;
        height: 35px;
        margin-top: 1em;
    }
  }

.form > form > button {
    
    width: 55px;
    height: 55px;
    overflow: visible;
    margin-right: 1em;
    border-radius: 50px;
    border: 0;
    cursor: pointer;
    resize: none;
    background-repeat: no-repeat;
} 

.form > form > button:hover{
    color: #CBD2D9;
}

.textarea::-webkit-scrollbar {
    width: 10px;
}

.textarea::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: rgba(34, 34, 34, 0.39);
}

.img{   
    width: 25px;
    height: 25px;
}
.history{
    background-color: #B2B1B9;
    
}
div::-webkit-scrollbar {
    width: 10px;
}

div::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: rgba(34, 34, 34, 0.39);
}
textarea::-webkit-scrollbar {
    width: 10px;
}

textarea::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: rgba(34, 34, 34, 0.39);
}
.editForms {
    width: 100%;
    height: 90%;
    overflow: auto;
}

.editForms > div{
    margin: 1em;
    width: 90%;
    height: 100%;
}

.formColumm {
    display: flex;
    height: auto;
    margin-left: 2em;
    flex-direction: column;
}

.alert {
    color: red;
    font-size: 1.3em;
    font-weight: bold;
}

.textareaEdit {
    height: 100px;
    background-color: #525252;
    color: white;
    font-size: 2em;
    border: 0px;
    border-bottom: 2px solid #1b1b1b ;
    padding: 12px 20px;
    margin-bottom: 2em;
    resize: vertical;
    overflow: visible;
}

.boldTextArea {
    
    height: 50px;
    background-color: #525252;
    color:white;
    font-size: 2em;
    font-weight: bold;
    border: 0px;
    border-bottom: 2px solid #1b1b1b ;
    padding: 12px 20px;
    margin-bottom: 1em;
    resize: none;
    overflow: visible;
}

.submitsChanges{
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1em;
    width: 350px;
    height: 50px;
    border: 0;
    background-color: gray;
    color: white;
    font-size: 1.2em;
    font-weight: bold;
    border-radius: 12px;
    cursor: pointer;
}

/* Tablet */
@media (max-width: 1024x) {
    
    .submitsChanges{
        width: 150px;
        height: 55px;
    }

}

/* Telefono */
@media (max-width: 768px) {

    
    .submitsChanges{
        width: 150px;
        height: 55px;
    }
  }


.submitsChanges:hover{
    background-color: rgb(73, 73, 73);
}

.image {
    width: 100%;
    height: auto;
}
.empty {
    width: 55%;
    height: 50%;
}

.image > img {
    width: 100%;
    max-height: 1000px;
}

.marginButtom{
    margin-bottom: 1em;
}

.commentContainer > div{
    border-radius: 0px;
}