.container {
    width: 40%;
    height: 90%;
    margin: 3em;
    margin-right: 4em;
    overflow: auto;
}

.container::-webkit-scrollbar {
    width: 10px;
}

.container::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: rgba(34, 34, 34, 0.39);

}

.card {
    width: 100%;
    height: auto;
    border-radius: 10px;
    color: black;
    margin-bottom: 2em;
    background-color: #313131;
}

/* Deskop and phone */
@media (min-height: 768px) {
    .container {
        width: 100%;
        height: 90%;
        margin: 0px;
        margin-top: 0px;
        overflow: auto;
    }
  }

/* Tablet */
@media (max-width: 768px) {
    .container {
        width: 100%;
        height: 90%;
        margin: 0px;
        margin-top: 0px;
        overflow: auto;
    }
  }

.information {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(15, 1fr);
    grid-template-rows: repeat(2, 25px) auto repeat(3, 25px) auto;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.title {
    margin: 0px;
    font-weight: bolder;
    color: white;
    overflow-x: hidden;
    font-size: 2.2em;
    grid-area: 2 / 3 / 4 / 14;
}
.description {
    margin: 0px;
    overflow-x: hidden;
    color: white;
    margin-bottom: 2em;
    grid-area: 8 / 2 / 10 / 15;
}

/* Telefonos hasta 768px */
@media (max-width: 768px) {
    .title {    
        font-size: 1.5em;
    }
  }
.user{
    margin: 0px;
    font-size: 1.2em;
    color: white;
    font-weight: 300;
    grid-area: 4 / 3 / 6 / 10;
}

.time {
    margin: 0px;
    font-weight: 300;
    color: rgb(184, 184, 184);    
    grid-area: 8 / 2 / 6 / 10;
}


.history {
    grid-area: 1 / 15 / 3 / 16;
}

.history_icon {
    font-size:1.5em;
    color: rgba(255, 0, 0, 0.685);
}
.history:hover {
    fill:white;
}

.image {
    width: 100%;
    height: auto;
}

.image > img {
    width: 100%;
    max-height: 1000px;
}

.interactions{
    height: 50px;
    width: 100%;
    display: flex;
    color: white;
    justify-content: space-around;
    flex-direction: row;
    font-size: 1em;

}

.editMode{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 1em;
}

.editMode > button{
    border: 0;
    border-radius: 12px;
    width: 30px;
    background-color: #313131;
    height: 30px;
    cursor: pointer;
}

.editMode > button:hover{
    background-color: rgb(54, 54, 54);  
    cursor: pointer;
}   
.favorite_false {
    cursor: pointer;
    width: 20px;
    height: 20px;
    font-size: 2em;
    fill: white;
    stroke: goldenrod ;
    stroke-width: 3em ;

}

.favorite_false:hover{
    fill: goldenrod ;
    background-color: none;
    
}
.favorite_true {
    cursor: pointer;
    width: 20px;
    height: 20px;
    font-size: 2em;
    fill: goldenrod;
    stroke: goldenrod ;
    stroke-width: 3em ;

}

.favorite_true:hover{
    fill: white ;
    background-color: none;
    
}

.row{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.row > p {
    color: white;
    margin-left: 10px;
}

.row > button {
    border: 0;
    border-radius: 12px;
    width: 30px;
    background-color: #313131;
    height: 30px;
    cursor: pointer;
}

.row > button:hover
{
    background-color: white;

}

.noPostImg {
    width: 350px;
    height: 350px;
}

.noPostText {
    font-weight: bold;
    color: white;
    font-size: 2em;
}

.noPostTextThin{
    font-weight:300;
    color: white;
    font-size: 1em;
}

.noPostButton {
    width: 200px;
    height: 75px;
    font-size: 1em;
    background-color: white;
    font-weight: bold;
    border-radius: 12px;
    margin-top: 2em;

}

.noPostButton:hover{
    color: white;
    cursor: pointer;
}
