.create {
    border-top: green;
}
.loading-create {
    animation: loading-create 1.2s ease none ;
}

@keyframes loading-create {
    from {
        border-top:  10px solid #52f385;
        width: 0px;
        display: block;
        margin: auto;
    }

    to {
        width: 100%;
        margin: auto;
        margin-bottom: 0px;
        border-top:  10px solid #52f385;
        display: none;
    }
}
