.container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
	border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	max-width: 100%;
	min-height: 100vh;
}


.main{
    background: #424242;
    padding: 2rem;
    width: 75%;
    max-width: 48rem;
    border-radius: 0.375rem;
    margin-top: 16px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}


.form{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    align-items: center;
    justify-content: center;
}

.inputs{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.h1{
    color: #fafafa;
    font-size: 25px;
}

.username{
    background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.username:hover{
    box-shadow: 0 4px 16px #fafafa;
    transition: all 0.7s ease;
}

.password{
    background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.password:hover{
    box-shadow: 0 4px 16px #fafafa;
    transition: all 0.7s ease;
}

.error{
    width: 100%;
    text-align: center;
    background-color: #fafafa;
    color: black;
    border-radius: 0.25rem;
    padding: 0.75rem;
}

.error:hover{
    color: #fafafa;
    background: #d32f2f;
    box-shadow: 0 4px 16px #d50000;
    transition: all 0.7s ease;
}

.btn{
    border: 0;
    cursor: pointer;
    border-radius: 20px;
    font-weight: bold;
    width: 175px;
    height: 50px;
}

.btn:hover
{
    color: black;
    background-color: #fafafa;
    box-shadow: 0 4px 16px #e0f2f1;
    transition: all 0.7s ease;
}


.img{
    margin-left: 25px;
    width: 400px;
    height: 400px;
}


/* Responsive: 320px */

@media only screen and (max-width: 320px) {
    .img{
        display: none;
    }


}

/* Responsive 375px */
@media only screen and (max-width: 375px) {
    .img{
        display: none;
    }
}

/* Responsive 425px */
@media only screen and (max-width: 425px) {
    .img{
        display: none;
    }
}

/* Responsive: 768px */
@media only screen and (max-width: 768px) {
    .img{
        width: 300px;
        height: 300px;
    }
}