
.centerAppear {
	-webkit-animation: borderApear 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: borderApear 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
  @-webkit-keyframes borderApear {
    0% {
        width: 0px;  
        opacity: 1;
        color: white;
        font-size: 0px;

      }

      75%{
        color: white;
        font-size: 0px;

      }

      100% {
        width: 175px;
        opacity: 1;
        color: black;

      }
  }

  @keyframes borderApear {
    0% {
        width: 0px;  
        opacity: 1;
        color: white;
        font-size: 0px;

      }

      75%{
        color: white;
        font-size: 0px;

      }

      100% {
        width: 175px;
        opacity: 1;
        color: black;

      }
  }
  
/* Telefono */
  @media (max-width: 768px) {
    .centerAppear {
      -webkit-animation: borderApear-mobile 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
              animation: borderApear-mobile 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    }
      @-webkit-keyframes borderApear-mobile {
        0% {
            width: 0px;  
            opacity: 1;
            color: white;
            font-size: 0px;
    
          }
    
          75%{
            color: white;
            font-size: 0px;
    
          }
    
          100% {
            width: 75px;
            opacity: 1;
            color: black;
    
          }
      }
    
      @keyframes borderApear-mobile {
        0% {
            width: 0px;  
            opacity: 1;
            color: white;
            font-size: 0px;
    
          }
    
          75%{
            color: white;
            font-size: 0px;
    
          }
    
          100% {
            width: 75px;
            opacity: 1;
            color: black;
    
          }
      }
      
  }