.header {
    margin-top: 1em;
    display: grid;
    grid-template-columns: 20fr auto;
    grid-template-rows: 65px;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.logoContainer {
    grid-area: 1 / 1 / 2 / 3; 

}

.container {
    grid-area: 1 / 2 / 2 / 6; 
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}

.headerButton{
    background-color: #212121 ;
    border: 0;
    flex-grow: 0 ;
    cursor: pointer;
    font-weight: bold;
    width: 175px;
    height: 60px;
    color: white;

}
button > svg {
    font-size: 2em;
}

button > p {
    margin-top: 0.1em;
    margin-bottom: 0.4em;
    color: white;
    font-size: 1em;

}

/* Tablet */
@media (max-width: 1024x) {
    .container {
        grid-area: 1 / 1 / 2 / 6; 
    }
}

/* Telefono */

@media (max-width: 768px) {

    .container {
        grid-area: 1 / 1 / 2 / 6; 
    }

    .headerButton{
        width: 75px;
        
    }
    button > p {
        margin-top: 0px;
        margin-bottom: 0px;
        color: white;
        font-size: 0px;
    }
  }

.headerButton:hover{
    color: black;
}



